*{
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  flex-direction: row;
  min-height: 93.5vh;
  height: 93.5vh;
  overflow: hidden;
  background-image: url('./assets/image/image2.jpg');
  /* #3C8DBC blue klik dokter */
  /* #008080 tosca */
  /* #FFFFFF white admin lte */
  /* background-color: blue; */
  /* background-image: linear-gradient(to right,grey,#FFFFFF),url('./assets/image/image2.jpg'); */
  /* background-blend-mode: soft-light ; */
  background-size: contain;
  background-position: left;
  background-repeat: no-repeat;
  justify-content: flex-start;
  /* align-items: center; */
  padding: 20px;
  flex-direction: row;
  font-family: cursive;
  color: white;
  /*background: linear-gradient(to bottom right,black,white);*/
  background-color: #515151;
  /* opacity: 0.70; */
}
/* .isdark{
  background-color: #050505;
  opacity: 0.70;
  color:whitesmoke;
} */

.App-container-content{
  flex: 1;
  padding-bottom:100px;
}
.App-content::-webkit-scrollbar {
    display: run-in;
}

.App-content{
  padding: 20px;
  transition: step-end 2s linear 2s;
  height: 93vh;
  overflow-y:scroll;
  /* width: 100%; */
  box-sizing: border-box;
  scroll-behavior: smooth;
  padding-bottom: 100px;
}

.App-image{
  display: flex;
  width: 500px;
  flex-direction: column;
}

.App-image-text{
  color: #515151;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
  /* opacity: 70%; */
  display: none;
  background-image:linear-gradient(to bottom,black,white);
}


.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-column{
  flex-direction: column;
}

@media only screen and (max-width :1000px){
  .App{
    display: flex;
    flex-direction: column;
    background: #F8F9FA;
    color: #515151;
    overflow: auto;
    height:max-content;
    min-height: fit-content;
    padding: 0;
  }

  .App-image{
    background-color: #515151;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    color:whitesmoke;
    padding: 30px;

  }

  .App-logo{
    display: block;
    width: 150px;
    height: 150px;
    justify-content: center;
    margin-bottom: 12px;
    border: 5px solid whitesmoke;
  }

  .App-image-text{
    color: whitesmoke;
  }

  .App-content{
    padding: 20px;
    transition: step-end 2s linear 2s;
    /* height: 93vh; */
    /* overflow-y:scroll; */
    /* width: 100%; */
    /* display: flex; */
    /* justify-content: center;
    align-items: center; */
    box-sizing: border-box;
    scroll-behavior: smooth;
    padding-bottom: 100px;
  }
}

